import { Component } from '@angular/core';
declare var $:any;


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent {
	
  public title = 'The Fastest Cars App';

     ngAfterViewInit(){
           $(document).ready(function(){
            $('.card-action').addClass('animate')
           });
     }
  
}


